header {

  @include max-screen(959px){
    .brand {
      margin-left: 12px;
    }
  }
  @include max-screen(560px){
    position: fixed !important;

    nav {

      .nav-wrapper {
        left: auto;
        .tucked-menu {
          left: auto;
          right: 0;
          margin: 0;

          .menu-tabs.tabs {
            &.pc  {
              display: none;
            }
            &.mobile {
              display: block;
              font-weight: bold;

              a {
                font-size: 16px;
              }
            }

            /* 为了移动端登陆之后能看到基本的信息打的补丁 */
            .tab:last-child{
              &:last-child {
                display: none;
              }
              &:first-child {
                display: block;
              }
            }
          }
        }
      }
      .nav-user {
        display: none;
      }
    }

    @include max-screen(960px) {
      .nav-user {
        margin-right: 12px;
      }
    }
    .nav-btn-login {
      display: none;
    }
  }
}
