select {
  display: none;
}

select.browser-default {
  display: block;
}

// Disabled styles
select:disabled {
  color: rgba(0,0,0,.3);
}

select:focus {
  outline: 1px solid lighten($secondary-color, 47%);
}

.select-wrapper input.select-dropdown:disabled {
  color: rgba(0,0,0,.3);
  cursor: default;
  -webkit-user-select: none; /* webkit (safari, chrome) browsers */
  -moz-user-select: none; /* mozilla browsers */
  -ms-user-select: none; /* IE10+ */
  border-bottom: 1px solid rgba(0,0,0,.3);
}

.input-field {

  .select-wrapper {
    position: relative;
     
    input.select-dropdown {
      display: inline-block;
      background: #FFF;
      box-sizing: border-box;
      border: 1px solid #FFF;
      border-radius: 2px;
      padding: 0 8px 0 12px;
      border: 1px solid #E6E6E6;
      @include border-radius(2px);
      outline: none;
      height: 35px;
      width: 100%;
      @extend .input-field-font;
      margin: 0;
      box-shadow: none;
      @include box-sizing(border-box);
      transition: all .3s;
    }
    /* 顶部的上下箭头 */
    .arrow-container {
      position: absolute;
      width: 25px;
      height: 100%;
      @include border-radius(2px);
      right: 0;
      border: 1px solid #E6E6E6;
      z-index: 1;

      i {
        top: 16px;
        right: 8px;
        z-index: 1;
      }
    }

    .dropdown-content {
      li {
        padding: 0;
        font-size: 0;

        &:hover {

          ul {
            display: block;
            @extend .z-depth-1;
          }
        }
        span {
          padding: 6px;
          padding-left: 12px;
          font-size: 12px;
        }

        span:not(:last-child) {
          display: inline-block;
          width: 90%;
        }

        .select-addon {
          display: inline-block;
          width: 10%;

          i {
            position: relative;
            left: 50%;
            /* 抵消select-wrapper i的样式  */
            top: 0;
          }
        }
      }
    }
  }
}

.select-wrapper i {
  position: absolute;
  right: 0;
  top: 20px;
  color: rgba(0,0,0,.3);
}

.select-dropdown li.disabled {
  color: rgba(0,0,0,.3);
  background-color: transparent;
}

select {
  background-color: rgba(255, 255, 255, 0.90);
  width: 100%;
  padding: 5px;
  border: 1px solid #f2f2f2;
  border-radius: 2px;
  height: 3rem;
}
