.pure-icon {
  box-sizing: content-box;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
  }
  &.success {
    padding-left: 28px;
    color: #C2C5B7;

    &::before {
      left: 6px;
      width: 5px;
      height: 12px;
      border-width: 0 2px 2px 0;
      border-style: solid;
      border-color: #C2C5B7;
      margin-top: -8px;
      -webkit-transform: rotate(45deg);
      -moz-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      -o-transform: rotate(45deg);
      transform: rotate(45deg);

    }
  }

  &.left-arrow {
    padding-left: 28px;
    color: #C2C5B7;
    height: 15px;
    top: 0px;
    left: -10px;
    cursor: pointer;

    &:before {
      left: 17px;
      width: 8px;
      height: 8px;
      border-width: 0 0 2px 2px;
      border-style: solid;
      margin-top: -4px;
      background: transparent;
      transform: rotate(45deg);
    }
  }
  &.right-arrow {
    padding-left: 28px;
    color: #C2C5B7;
    height: 15px;
    cursor: pointer;

    &:before {
      left: 8px;
      width: 8px;
      height: 8px;
      border-width: 0 2px 2px 0;
      border-style: solid;
      margin-top: -4px;
      background: transparent;
      transform: rotate(-45deg);
    }
  }
}

/* 箭头样式 上下左右 */
.arrow {
  display: inline-block;
  width: 0;
  height: 0;
  /* margin-bottom: 2px; */

  &.arrow-down,
  &.arrow-up {
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
  }

  &.arrow-down{
    border-top: 5px solid rgba(0,0,0,0.28);
  }

  &.arrow-up{
    border-bottom: 5px solid rgba(0,0,0,0.28);
  }

  &.arrow-left,
  &.arrow-right {
    border-top: 4px solid transparent;
    border-bottom: 4px solid transparent;
  }

  &.arrow-left {
    border-right: 5px solid rgba(0,0,0,0.28);
  }

  &.arrow-right {
    border-left: 5px solid rgba(0,0,0,0.28);
  }
}