/* .btn: 常规按钮 */
/* .btn-flat：线框按钮 */
/* .btn-ghost：幽灵按钮 */
/* 将整个button的样式同一打包放在这里 */

.btn,
.btn-flat,
.btn-ghost,
.btn-simple,
.dark-btn{
  display: inline-block;
  border: none;
  @include border-radius(2px);
  height: $button-height;
  min-width:$button-width;
  line-height: $button-line-height;
  padding: 0 12px;
  margin-right: 16px;

  outline: 0;
  font-size: 14px;
  text-transform: uppercase;
  text-decoration:none;
  text-align: center;
  letter-spacing: .5px;
  vertical-align: middle;
  // Gets rid of tap active state
  -webkit-tap-highlight-color: transparent;
  cursor:pointer;
}
// Disabled shared style
.btn-ghost.disabled,
.btn.disabled,
.btn-flat.disabled,
.btn-simple.disabled,
.btn:disabled,
.btn-ghost:disabled,
.btn-flat:disaled,
.btn-simple:disabled{
  background-color: $button-bg-color-disabled;
  box-shadow: none;
  color: $button-color-disabled;
  cursor: pointer;

  &:hover,
  &:active {
    background-color: $button-bg-color-disabled;
    box-shadow: none;
    color: $button-color-disabled;
    cursor: not-allowed;
  }
  * {
    pointer-events: none;
  }
}

/* 常规按钮 */
.btn {
  /* #FFFFFF */
  color: $button-font-color;
  /* $button-color: #20C05C !default; */
  background-color: $button-color;

  &.dark {
    color: #FFF;
    background-color: transparent;
    border: 1px solid #FFF;
  }

}
// 线框按钮
.btn-flat {
  box-shadow: none;
  background-color:$button-bg-color-flat;
  border:1px solid $button-color;
  color: $button-font-color-flat;
  * {
    color: $button-font-color-flat;
  }

  &.dark {
    border:1px solid $button-dark-color;
    color: $button-dark-color;
    * {
      color: $button-dark-color;
    }
  }
}
/* 常规按钮和线框按钮仅常规状态不同 */
.btn,
.btn-flat{

  &:hover{
    color: $button-font-color;
    background-color: $button-hover-color;
    * {
      color: $button-font-color;
    }
  }
}
.btn.dark:hover {
  color: #FFF;
  background-color: $button-dark-color;
  * {
    color: #FFF;
  }
}
.btn-flat.dark:hover {
  color: $button-dark-color;
  background-color: #FFF;

  * {
    color: $button-dark-color;
  }
}
  /* &:active{
   * {
     color: $button-font-color;
     }
     background-color: $button-active-color;
     } */

/* 幽灵按钮 */
.btn-ghost{
  color: $button-font-color-ghost;
  /* $button-bg-color-ghost: #FFF !default; */
  background-color:$button-bg-color-ghost;
  &:hover{
    background-color: $button-hover-color-ghost;
  }
  &:active{
    background-color: $button-active-color-ghost;
  }
}

/* 不重要按钮 */
.btn-simple{
  color: $button-font-color-simple;
  /* $button-bg-color-ghost: #FFF !default; */
  background-color:$button-bg-color-simple;

  &:hover{
    color: $button-font-color;
    background-color: $button-hover-color-simple;
  }
  &:active{
    color: $button-font-color;
    background-color: $button-active-color-simple;
  }
}
.mini-btn {
  display: inline-block;
  border: 1px solid #FAFAFA;
  @include border-radius(2px);
  /* height: 24px; */
  min-width: 48px;
  line-height: 24px;
  padding: 0 8px;
  margin-right: 8px;
  background: #FFF;
  outline: 0;
  text-decoration:none;
  text-align: center;
  cursor:pointer;

  @extend .mini-btn-font;

}
/* accpet: 面试成功|接受|职位标签
   refuse： 面试拒绝
   negociate： |不合适 */

.mini-state-btn {
  @extend .mini-btn;
  margin-right: 8px;
  $mini-btn-arr: (
    "inprogress": $process-color,
    "refuse": $reject-color,
    "negociate": $notype-color,
    "success": $success-color,
    "accept": $success-color,
    "unfit": $failed-color
  );

  @each $name, $color in $mini-btn-arr {
    &.#{$name} {
      border: 1px solid $color;
      color: $color;
      padding: 0 8px;
      line-height: 24px;
      &:hover,
      &.active {
        background: $color;
        color: #FFF;
      }
    }
  }
}
