// Text inputs

/***************************
   Text Inputs + Textarea
 ****************************/

// Style Placeholders
::-webkit-input-placeholder {
  color: $input-placeholder-color;
}

:-moz-placeholder { /* Firefox 18- */
  color: $input-placeholder-color;
}

::-moz-placeholder {  /* Firefox 19+ */
  color: $input-placeholder-color;
}

:-ms-input-placeholder {
  color: $input-placeholder-color;
}

input[type=text],
input[type=password],
input[type=email],
input[type=url],
input[type=time],
input[type=date],
input[type=datetime-local],
input[type=tel],
input[type=number],
input[type=search],
textarea.materialize-textarea {

  // General Styles
  background-color: #FFF;
  border: 1px solid $input-border-color;
  color: rgba(0,0,0,0.87);
  padding-left: 8px;
  @include border-radius(2px);
  outline: none;
  height: 36px;
  width: 100%;
  @extend .input-field-font;
  margin-bottom: 8px;
  box-shadow: none;
  @include box-sizing(border-box);
  transition: all .3s;

  // Disabled input style
  &:disabled, &[readonly="readonly"] {
    color: $input-disabled-color;
    border: 1px dotted $input-disabled-color;

  }
  // Disabled label style
  &:disabled+label, &[readonly="readonly"]+label {
    color: $input-disabled-color;
  }
  // Focused input style
  &:focus:not([readonly]) {
    border: 1px solid $input-focus-color;
  }
  // Focused label style
  &:focus:not([readonly])+label {
    color: $input-focus-color;
  }
  // Valid Input Style
  &.valid,
  &:focus.valid {
    border: 1px solid $input-success-color;
  }
  // Custom Success Message
  &.valid + label:after,
  &:focus.valid + label:after {
    content: attr(data-success);
    color: $input-success-color;
    opacity: 1;
  }
  // Invalid Input Style
  &.invalid,
  &:focus.invalid {
    border: 1px solid $input-error-color;
  }
  // Custom Error message
  &.invalid + label:after,
  &:focus.invalid + label:after {
    content: attr(data-error);
    color: $input-error-color;
    opacity: 1;
  }
}

// Styling for input field wrapper
.input-field {
  position: relative;
  /* height: 60px; */
  margin-top: 20px;

  input:after{
    display: block;
    content: attr(data-error);
    position: absolute;
    top: 55px;
    opacity: 1;
    /* transition: .2s opacity ease-out, .2s color ease-out; */

  }
  label {
    position: absolute;
    top: -20px;
    left: 0;
    font-size: 12px;
    color: rgba(0,0,0,0.54);
    /* @extend .input-label-font; */
    cursor: text;
    /* @include transition(.2s ease-out); */
  }
  label.active {
    /* @include transform(translateY(-140%)); */
  }

  .help-block{
    display: inline-block;
    width: 100%;
    @extend .input-field-error-font;
  }

  // Prefix Icons
  .prefix {
    position: absolute;
    width: 3rem;
    font-size: 2rem;
    @include transition(color .2s);

    &.active { color: $input-focus-color; }
  }
  .prefix ~ input,
  .prefix ~ textarea {
    margin-left: 3rem;
    width: 92%;
    width: calc(100% - 3rem);
  }
  .prefix ~ textarea { padding-top: .8rem; }
  .prefix ~ label { margin-left: 3rem; }

  @media #{$medium-and-down} {
    .prefix ~ input {
      width: 86%;
      width: calc(100% - 3rem);
    }
  }
  @media #{$small-and-down} {
    .prefix ~ input {
      width: 80%;
      width: calc(100% - 3rem);
    }
  }
}

/* 自定义 */
.input-field {
  .select-wrapper {
      position: relative;
    input.select-dropdown {
      display: inline-block;
      background: #FFF;
      box-sizing: border-box;
      border: 1px solid #FFF;
      border-radius: 2px;
      padding: 0 8px 0 12px;
      border: 1px solid #E6E6E6;
      @include border-radius(2px);
      outline: none;
      height: 35px;
      width: 100%;
      @extend .input-field-font;
      margin: 0;
      box-shadow: none;
      @include box-sizing(border-box);
      transition: all .3s;
    }
    .arrow-container {
      position: absolute;
      width: 25px;
      height: 100%;
      @include border-radius(2px);
      right: 0;
      border: 1px solid #E6E6E6;
      z-index: 1;

      i {
        top: 16px;
        right: 8px;
        z-index: 1;
      }
    }
  }
}

.input-field.text-field {

  margin-top: 0;

  textarea {
    background-color: #FAFAFA;
    border: none;
    border-radius: 0;
    outline: none;
    height: 114px;
    width: 100%;
    resize: none;
    padding: 8px;
    box-shadow: none;

    font-size: 14px;
    letter-spacing: 1px;

  }

  label {
    position: relative;
    top: -4px;
  }
}
