/* card */
.card{
    label{
        @include font-size(12px);
        color:lighten(black,46%);
    }
    .card-content-wrapper{
        position:relative;
        background:#1FBF5B;
        height:150px;
        color:white;
        .card-content{
            position:absolute;
            padding:16px;
            bottom:0;
            /* todo-写入card样式 */
            .card-title{
                @include font-size(24px);
            }
            .card-caption{
                @include font-size(14px);
            }
            p,span{
                display:inline-block;
                width:100%;
            }
        }
    }
}
.card-list{
    .title{
        @include font-size(16px);
    }
    .list-item{
        @include font-size(16px);
    }
}

