.card-panel {
    padding: $card-padding;
    margin: $element-top-margin 0 $element-bottom-margin 0;
    border-radius: 2px;
    @extend .z-depth-1;
    /* background-color: $card-bg-color; */
}

.card,
.card-center{
    position: relative;
    overflow: hidden;
    margin: $element-top-margin 0 $element-bottom-margin 0;
    border-radius: 2px;
    background: $card-content-bg-color;
    @extend .z-depth-1;

    .card-header{
        position: relative;
        background-color: $card-bg-color;
        padding: $card-padding;
    }
    .card-content {
        position: relative;
        border-radius: 0 0 2px 2px;
        p, form {
            margin: 0;
          /* padding-top: 16px; */
          padding-top: 8px;
        }
    }

    .card-header.large{
        height:150px;
    }
    .card-header.medium{
        height:120px;
    }

    .card-title {
        @extend .card-title-font;
        &.activator {
            cursor: pointer;
        }
    }
    
    .card-caption {
        font-size: 14px;
    } 
    // Card Sizes
    &.small, &.medium, &.large {
        position: relative;

        .card-image {
            overflow: hidden;
        }
        .card-content {
            overflow: hidden;
        }
        .card-action {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
        }
    }

    &.small {
        height: 300px;

        .card-image {
            height: 150px;
        }
        .card-content {
            height: 150px;
        }

    }

    &.medium {
        height: 400px;

        .card-image {
            height: 250px;
        }
        .card-content {
            height: 150px;
        }
    }

    &.large {
        max-width: 680px;
    }

    /* .card-action { */
    /*     /\* padding: $card-padding; *\/ */

    /*     a { */
    /*         color: color("orange", "accent-2"); */
    /*         margin-right: $card-padding; */
    /*         @include transition(color .3s ease); */
    /*         text-transform: uppercase; */

    /*         &:hover { color: lighten(color("orange", "accent-2"), 20%); } */
    /*     } */
    }

    .card-reveal {
        padding: $card-padding;
        position: absolute;
        background-color: #FFF;
        width: 100%;
        overflow-y: auto;
        top: 100%;
        height: 100%;
        z-index: 1;
        display: none;

        .card-title {
            cursor: pointer;
            display: block;
        }

    }
/* public style */
.card{
    position:relative;
    margin:0 auto;
    padding-bottom:16px;
    width:100%;
    max-width:360px;
    .card-text-content{
        position:absolute;
        padding-bottom:16px;
        bottom:0;
        color: #FFF;
    }
    .card-field-content{
        position:relative;
        /* 里面的文字框已经有了16px的间距 */
        padding: 32px 24px 0 24px;
        form{
            width:100%;
            .form-content{
                width:100%;
                position:relative;
                input{
                    border-color:rgba(0,0,0,0.28);
                }
            }
            .input-field{
                padding:0px;
            }
            label{
                /* 原本有9px的左偏移,9.6px的上偏移 */
                left:0;
            }
        }
    }
    .card-action{
        position:relative;
        background-color:white;
        padding:6px 0  8px 0;
        /* padding:6px 16px 8px 16px; */
        height:56px;
        width:100%;
        button{
            positon:relative;
        }
    }
}

.card-list{
    position:relative;
    margin:0 auto;
    padding-bottom:16px;
    width:100%;
    max-width:$card-list-width;
    max-height:$card-list-height;
    box-sizing:border-box;
    @include prefix(box-shadow,0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12));
    @include border-radius(2px);
    .card-content-company{
        width:100%;
        position:relative;
        height:$card-list-height;
        .card-content-logo{
            position:relative;
            display:block;
            float:left;
            height:100%;
            width: $card-content-logo-width;
            img{
                height:100%;
                width:auto;
                max-width:100%;
                max-height:100%;
            }
        }
        .card-info-content{
            position:relative;
            display:block;
            float:left;
            width:$card-list-width - $card-content-logo-width - 1;
            .company-info{
                padding:24px 8px 16px 24px;
                border-bottom:1px solid lighten(black,80%);
                .title-container{
                    color:lighten($collection-text-color,16%);
                }
                .list-container{
                    span{
                        color:lighten(black,46%);
                    }
                }
            }

        }
    }
    .title-container,.list-container{
        position:relative;
        width:100%;
        display:inline-block;
    }

}
