// 用于单页面整幅card
// 用于修改弹窗
.section-card {
  @include border-radius(2px);
  @extend .z-depth-1;
  width: 100%;
  background: $primary-content-color;
  text-align: left;
  h1 {
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    @extend .invitation-content-font;
    font-weight: 900;
  }

  .card-header {
    position: relative;
    width: 100%;
    height: $section-content-header-height;
    background: $gray-header-color;
    border-bottom: 1px solid #EEE;
    @extend .common-title-font;

    .header-title {
      /* 和position中list-title的样式一致，后续剥离出来 */
      position: absolute;
      left: 24px;
      display: inline-block;
      height: $section-content-header-height;
      line-height: $section-content-header-height;

      a {
        color: rgba(0,0,0,0.87);
      }
    }

    .header-content {
      display: inline-block;
      vertical-align: top;
      height: $section-content-header-height;
      padding-right: $header-right-padding;
    }
    /* 和常规卡片的.header-action样式相同 */
    .header-action {

      position: absolute;
      top: 16px;
      right: 16px;
      display: inline-block;
      width: 240px;
      text-align: right;
      z-index: 3;

      a {
        @extend .list-action-font;
        cursor: pointer;
        display: inline-block;
        padding: 0 8px;
        color: $primary-color;
      }
      a.light {
        font-weight: 100;
        color: rgba(0,0,0,0.54);
      }
    }
  }

  .card-content {
    padding: 16px 24px;

    .content-action {
      height: 48px;
      line-height: 48px;

      a {
        display: inline-block;
      }
    }
  }
}

.details-action {
  text-align: center;
  padding: 24px 0 0 24px;
}
textarea.jx {

  background-color: #FAFAFA;
  border: none;
  border-radius: 0;
  outline: none;
  /* height: 200px; */
  width: 100%;
  resize: none;
  padding: 8px;
  margin-top: 12px;
  box-shadow: none;
  font-size: 14px;
  letter-spacing: 1px;
}

.panel-container {

  .panel-title {
    font-size: 14px;
    line-height: 32px;
  }
  .panel-content {
    padding: 12px 16px;
    border: 1px solid #E6E6E6;
    @include border-radius(2px);
    margin-bottom: 12px;
  }
  .panel-new {
    cursor: pointer;
    border: 1px solid #E6E6E6;
    @include border-radius(2px);
    box-sizing: border-box;
    display: inline-block;
    width: 100%;
    line-height: 32px;
    text-align: center;
    color: rgba(0,0,0,0.54);
  }
}
