.dropdown-content {
    @extend .z-depth-nav;
    margin: 0;
    display: none;
    min-width: 100px;
    max-height: 650px;
    position: absolute;
    z-index: 99;
    will-change: width, height;

    li {
        background-color: #fff; 
        clear: both;
        color: $off-black;
        cursor: pointer;
        padding: 8px;
        width: 100%;
        text-align: left;
        text-transform: none;

        &:hover, &.active {
            background-color: #eee;
        }

        & > a, & > span {
            display: block;
            /* height: 48px; */
            width: 100%;
            opacity: 1;
            color: rgba(0,0,0,.54);
        }
    }
    /* 箭头 */
    i.ul-arrow {
        background-color: transparent;
        display: block;
        position: relative;
        height: 8px;
        width: 100%;
        z-index: -1;

        a {
            display: inline-block;
            position: absolute;
            top: 0;
            right: 7px;
            width: 0 !important;
            height: 0 !important;
            border-style: solid;
            border-width: 0 18px 12px 18px;
            border-color: transparent transparent #FFF transparent;
            line-height: 0px;
            _border-color: #FFF;
            _filter: progid:DXImageTransform.Microsoft.Chroma(color='#000000');
        }
    }
}
