$navbar-dividor-color: #EBEBEB !default;
$navbar-dropdown-hover-color: #EBEBEB !default;

header {
  z-index: 10;
  position: absolute;
  height: $header-height;
  width: 100%;
  background-color: $navbar-bg-color;
  text-align: left;
}

nav {
  width: 100%;
  height: $navbar-height;
  margin-top: $navbar-margin-top;
  color: $navbar-font-color;
  font-size: 14px;
  @extend .z-depth-nav;

  .nav-wrapper {
    position: absolute;
    margin: auto;
    /* 不支持calc的 */
    left: 0;
    left: calc(100vw - 100%);
    right: 0;
    width: 100%;
    max-width: 960px;
    height: $navbar-height;

    /* logo的样式 */
    .brand {
      position: relative;
      height: 34px;
      width: 130px;
      display: inline-block;
      vertical-align: top;
      box-sizing: border-box;
      margin-top: 4px;

      img {
        @extend .center;
        max-width:100%;
        max-height:100%;
      }
    }

    /* 导航栏内容样式 */
    .tucked-menu {
      position: absolute;
      top: 0;
      left: 56px;
      display: inline-block;
      margin: 0 78px;

      .mobile {
        display: none;
      }

      ul {
        position: relative;
        width: 100%;
        margin: 0;
        height: $navbar-height;

        li {
          float: left;
          height: 100%;
          line-height: $navbar-height;
          padding-left: 24px;
          padding-right: 24px;
          width: auto;

          a {
            display: inline-block;
            color: rgba(0,0,0,0.54);
            font-size: 14px;

            &:hover, &.active {
              color: $navbar-font-hover-color;
            }
          }
        }
      }

    }

    .indicator {
      position: absolute;
      bottom: 0;
      height: 2px;
      background-color: $tabs-underline-color;
      will-change: left, right;
    }

  }
}

.nav-user{
  position: absolute;
  /* TODO：如果取消 top:0  在简寻中会出现nav-user直接出现在navbar的下面的情况 ，亟需明白原因*/
  top: 0;
  right: 0;
  width: 120px;
  display: inline-block;
  height: $navbar-height;
  color: #000;

  .nav-user-container {
    display: inline-block;
    position: relative;
    height: $navbar-height;
    margin: 0;

    .nav-user-item {
      @include size(48px);
      display: inline-block;
      position: relative;
      margin-left: 10px;

      img {
        position: absolute;
        margin: auto;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        border-radius: 36px;
      }

      a {
        display: inline-block;
        height: 48px;
        width: 100%;
        opacity: 1;
        padding: 0;
      }
    }

    ul.content-container {

      li {
        margin: 0;
      }
    }

    li.dropdown {

      img {
        height: 36px;
        width: 36px;
      }

      .dropdown-button {
        cursor: pointer;
      }

      &:hover > ul.dropdown-content {
        display: block;
      }
    }
  }
  /* 下拉菜单的样式 */
  ul.dropdown-content {
    width: 128px;
    right: 0;
    top: 48px;
    background: transparent;

    li {
      background: $navbar-bg-color;
      text-align: center;
      padding: 0px;
       
      a {
        line-height: 48px;
        padding: 0 16px;
        font-size: 14px;
        &:hover{
          background-color: $navbar-dropdown-hover-color;
        }
      }

      &:last-child{
        border-top: 1px solid $navbar-dividor-color;
      }
    }
  }
}

/* 未登录button样式 */
.nav-btn-login{
  position: absolute;
  width: 108px;
  height: 26px;
  margin: auto;
  right: 0;
  top: 0;
  bottom: 0;
  font-size: 0;
  line-height: 26px;
  background-color: #fafafa;
  a{
    display: inline-block;
    text-align: center;
    height: 100%;
    line-height: 26px;
    font-size: 14px;

    width: 50%;
    border:0;
    color: $navbar-font-color;

    &:hover{
      background-color: $primary-color;
      color: $navbar-bg-color;
      cursor: pointer;
    }
  }
}

.navbar-menu-infobox {

  .tooltipped {
    display: inline-block;

    span {
      text-align: center;
      background-color: #e6395e;
      color: white;
      font-size: 12px;
      line-height: 18px;
      position: absolute;
      width: 18px;
      height: 18px;
      left: 30px;
      top: 6px;
      border-radius: 9px;
    }
  }
}

.notify-card {
  width: 300px;
  top: 48px;
  right: -6px;
  font-size: 0;

  .card-header {
    width: 100%;
    height: 48px;
    background: #FFF;
    position: relative;
    font-size: 14px;
    @extend .bottom-border;

    .header-title {
      position: absolute;
      left: 16px;
      display: inline-block;
      line-height: 48px;

      * {
        color: rgba(0,0,0,0.54);
      }
    }

    .header-action {
      position: absolute;
      right: 16px;
      display: inline-block;
      text-align: right;

      &:hover {
        a {
          color: $primary-color;
        }
      }

      a {
        @extend .list-action-font;
        width: auto;
        line-height: 48px;
        cursor: pointer;
        display: inline-block;
        padding: 0 8px;
        color: rgba(0,0,0,0.54);
      }
    }
  }

  .card-content {
    width: 100%;
    max-height: 288px;
    min-height: 72px;
    overflow-y: auto;
    overflow-x: hidden;

    ul.content-container {
      width: 100%;
      height: auto;
    }

    .info-item {
      display: inline-block;
      height: 72px;
      width: 100%;
      padding: 16px;
      @extend .bottom-border;
      background: #FFF;
      cursor: pointer;
      font-size: 14px;

      span {
        color: rgba(0,0,0,0.54);
      }

      &.none {
        text-align: center;
        color: rgba(0,0,0,0.28);
        line-height: 72px;
        padding: 0;
        &:hover {
          background: #FFF;
        }
      }

      &.unread {
        border-left: 2px solid $primary-color;
      }

      &:hover {
        background: #FAFAFA;
      }
    }
  }
}

/* 活动提示 */
.event-notify {
  cursor: pointer;
  position: absolute;
  top: -30px;
  height: 90px;
  width: 80%;
  margin: auto;
  left: 0;
  right: 0;
  max-width: 800px;
  z-index: 5;

  a {
    display: inline-block;
    @include size(100%);
  }

  img {
    max-width: 100%;
    max-height: 100%;
  }
  .event-cross {
    font-size: 26px;
    font-weight: bold;
    color: #6A3906;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 20px;
  }
}
